.HW1 {
    background-color: #a7e601;
}

.avatar {
    margin-left: 15px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    position: absolute;
    bottom: 10px;

}
.message {
    position: relative;
    height: 100px;
    color: #f7f8f6;
    display: grid;
    grid-template-columns: 100px 200px;

}

.messageImg {
    margin: auto;
    background-color: #002201;
    height: 80px;
    display: grid;
    grid-template-columns: 150px 50px;
    bottom: 10px;
    right: 5px;
    width: 200px;
    border-radius: 5px 5px 5px 0px;
}
.messageImg:before {
    content: '';
    position: absolute;
    width: 30px;
    height: 20px;
    left: 90px;
    bottom: 10px;
    border: 13px solid;
    border-color: transparent transparent #002201 transparent;
}



.name {
    color: #a8a4f5;
    margin-left: 5px;
}

.parent_of_time {
    position: relative;
}

.time {
    color: #2c702c;
    position: absolute;
    left: 5px;
    bottom: 5px;
}

.textMessage {
    margin-left: 5px;
}

